* {
  /* --color-light: #f8ede3;
  --color-med: #dfd3c3;
  --color-text: #b57491;
  --color-dark: #85586f; */
  --color-light: #f2eee0;
  --color-med: #c8e6f5;
  --color-text: #5ca0d3;
  --color-dark: #5e0a0a;

  margin: 0px;
  padding: 0px;
  /* border: 1px solid black; */
  box-sizing: border-box;
}

.container {
  height: 100vh;
  color: var(--color-dark);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10vh;
  /* background: linear-gradient(var(--color-light), var(--color-med)); */
  background: var(--color-light);
}

.ambigrams {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  align-self: flex-start;
  padding-left: 10%;
  padding-right: 10%;
  margin: -2em 0 2em 0;
  color: var(--color-text);
  font-size: 24px;
}
.intro-container {
  color: var(--color-dark);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 2em;
}

.intro {
  font-size: 36px;
}

.images-container {
  width: 90%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2em;
}

.links-row {
  display: flex;
  width: 40%;
  gap: 25%;
  justify-content: center;
  margin-top: 1em;
  padding: 1em 0 0 0;
}

.link-icon {
  color: var(--color-dark);
}

figure {
  color: var(--color-text);
}

figcaption {
  font-size: 1.2em;
}

@media (max-width: 800px) {
  .container {
    padding: 24px;
    display: block;
    height: auto;
  }

  .intro-container {
    padding: 48px;
  }

  .images-container {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
